<script>
import productDetailData from '../../assets/data/product-data.js'
import $ from 'jquery';

export default {
    name: 'product',
    data(){
      return {
        productDetailNavOpen: 0,
        list: productDetailData,
        productDetailTitle: ['Conventional Mold','M.G.P Mold',"Automold Chase & C'Kit",'Book Mold','Mold Spare Parts','진공탈포기','진공챔버','메가진','오페지 지그','CURE OVEN','살수시험기','열충격시험기','염수시험기','오토메틱시험기','항온항습기'],
        productSubNav1: ['Conventional Mold','M.G.P Mold',"Automold Chase & C'Kit",'Book Mold','Mold Spare Parts'],
        productSubNav2: ['진공탈포기','진공챔버','메가진','오페지 지그'],
        productSubNav3: ['CURE OVEN','살수시험기','열충격시험기','염수시험기','오토메틱시험기','항온항습기'],
        productSubNav1Open: 0,
        productSubNav2Open: 0,
        productSubNav3Open: 0,
        productSubNavRoute: [0,5,9],
        productSlide: 0,
        product0: ['K_SMP528 TOP CAVITY (FICO)','LBGA L MAP CULL & POT Block','LBGA LMAP BTM Cavity Bar','osprey sip 1.5 Top Cavity Bar','PBGA 23x23 top cvity Bar','PBGA 35x35 Top Cavity Bar 2','PBGA 35x35 Top Cavity Bar','SOIC 8LD Top & Btm Cavity Bar','SOT 23 56LD Btm Cavity Bar','SOT 23 56LD Top Cavity Bar','TSOP48 Top & Btm cavity, top cull block'],
        product1: ['title0','title1','title2'],
        product2: ['ASA MICRO SD C-KIT','ASA MICRO SD C-KIT','DAI-ICHI C-KIT','Deculler','Large-QFN c-kit','towa C-KIT'],
        product3: ['FS BOOK MOLD 1','FS BOOK MOLD 2','VR BOOK MOLD 1','VR BOOK MOLD2'],
        product4: ['title0','title1','title2','title3','marking pin','set block','TRACK COVER REAR','PBGA Gate Insert','PIn Gate','Top Gate Insert'],
        product5: ['title0','title1'],
        product6: ['title0','title1'],
        product7: ['title0'],
        product8: ['CERAMICS PKG MZ TYPE AND BOAT TYPE','CERAMICS PKG MZ TYPE','DS PKG TYPE'],
        product9: ['BAKE OVEN (4 zone)','N2 CLEAN OVEN (1 zone)','N2 CLEAN OVEN (2 zone)','N2 CLEAN OVEN (4 zone)'],
        product10: ['title0','title1','title2','title3'],
        product11: ['title0','title1','title2'],
        product12: ['title0'],
        product13: ['title0','title1','title2','title3'],
        product14: ['title0','title1','title2'],
      }
    },
    props: {
      productSubNav1View: String,
      productSubNav2View: String,
      productSubNav3View: String,
      productSubTitle: String,
    },
    methods: {
      productSubNavViewFn(z){
        this.$emit('setProductSubNavView', z);
        this.productDetailNavOpen = 0;
      },
      productSlideFn(i){
        this.productSlide = i;
        
        $('.pro-slideBtn').removeClass('clicked');
        $('.pro-slideBtn').eq(i).addClass('clicked');
      },
      getSrcA(i){
        return require('../../assets/image/sub/product/conventional-mold/cm'+i+'.jpg');
      },
      getSrcB(i){
        return require('../../assets/image/sub/product/c-kit/c-kit'+i+'.jpg');
      },
      getSrcC(i){
        return require('../../assets/image/sub/product/mold-spare-part/msp'+i+'.jpg');
      },        
      getSrcD(i){
        return require('../../assets/image/sub/product/tester-a/ta'+i+'.jpg');
      },        
      getSlideSrcA(i){
        return require('../../assets/image/sub/product/mgp-mold/mm'+i+'.jpg');
      },      
      getSlideSrcB(i){
        return require('../../assets/image/sub/product/book-mold/bm'+i+'.jpg');
      },      
      getSlideSrcC(i){
        return require('../../assets/image/sub/product/chamber/chamber'+i+'.jpg');
      },      
      getSlideSrcD(i){
        return require('../../assets/image/sub/product/vacuum/vc'+i+'.jpg');
      },      
      getSlideSrcE(i){
        return require('../../assets/image/sub/product/mega-jin/mj'+i+'.jpg');
      },      
      getSlideSrcF(i){
        return require('../../assets/image/sub/product/omega-jig/oj'+i+'.jpg');
      },      
      getSlideSrcG(i){
        return require('../../assets/image/sub/product/cure-oven/co'+i+'.jpg');
      },      
      getSlideSrcH(i){
        return require('../../assets/image/sub/product/tester-b/tb'+i+'.jpg');
      },      
      getSlideSrcI(i){
        return require('../../assets/image/sub/product/tester-c/tc'+i+'.jpg');
      },      
      getSlideSrcJ(i){
        return require('../../assets/image/sub/product/tester-d/td'+i+'.jpg');
      },      
      getSlideSrcK(i){
        return require('../../assets/image/sub/product/tester-e/te'+i+'.jpg');
      },      
    
    }
}
</script>

<style>

.product-visual {
  width: 100%; height: 440px;
  background: url('../../assets/image/sub/productVisual.jpg');
  background-size: cover;
  background-color: #0005;
  background-blend-mode: darken;  
  background-position: center center; 
}

.product-visual h4 {
  font-size: 54px; color: #fff; font-weight: bold;
  text-align: center;
  padding: 193px 0;
}

.product-content {
  width: 100%;
}



.product-list {
  padding-top: 50px;
  padding-bottom: 50px;
  border-bottom: 1px solid rgba(0,0,0, 0.3);
}

.product-list:first-child {
  padding-top: 0px;
}

.product-list:last-child {
  padding-bottom: 0px;
  border-bottom: 0;
}

.product-list-title {
  padding-bottom: 10px;
}

.product-list-title p {
  font-size: 20px;
  font-weight: bold;
}


.product-list-content>span {
  display: inline-block;
  height: 300px;
}

.product-list-content>span>img {
  height: 100%;
}

.product-list-content>span>span {
  display: block;
  padding-left: 30px;
}




.product-slide-main {
  display: block;
  float: left;
  width: 40%;
  padding-right: 5%;
}

.product-slide-main span {
  display: block;
  border: 1px solid #ddd;  
}


.product-slide-main img {
  width: 100%;
}

.product-slide-text {
  display: block;
  float: left;
  width: 55%;
  height: 558px;
  position: relative;
}

.product-slide-text h3 {
  font-size: 32px;
  font-weight: bold;
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #ddd;
}

.product-slide-text p {
  font-size: 18px;
}


.product-slide-button {
  display: block;
  position: absolute;
  bottom: 0; left: 0;
}


.product-slide-button li {
  float: left;
  padding-right: 20px;
}

.product-slide-button a {
  display: block;
  width: 150px;
  border: 2px solid #ddd;
}

.product-slide-button a.clicked {
  border: 2px solid rgba(235,100,64, 1);
}


.product-slide-button img {
  width: 100%;
}




/* response */

@media all and (max-width:1024px){
	.product-visual {
		height: 340px;
